* {
    margin: 0;
}
.App {
    margin: auto;
    max-width: 1100px;
    display: grid;
    grid-template-areas: 
    'header'
    'content';
    grid-template-rows: 50px 1fr;
}
.header {
    grid-area: header;
    background-color: #B8E1FF;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}
.box-content {
    grid-area: content;
}