.description {
    margin: 3px;
    padding: 5px;
    width: 200px;
    height: auto;
    border: black 0px solid;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.description:hover {
    background-color: #B8E1FF;
    transition: .3s;
}
.imageFrame {
    display: flex;
    justify-content: center;
    border: inherit;
    border-radius: inherit;
}
.image {
    margin: 3px;
    max-width: 200px;
    max-height: 300px;
    border-radius: 10px;
}
.text {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.text p {
    margin: 2px;
}
.btnInfo {
    display: flex;
    justify-content: center;
}
.btnInfo button {
    text-transform: uppercase;
}
