.productsContainer {
    display: flex;
}
.listBox {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.productsList {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.navigation {
    margin-top: 10px;
    position: sticky;
    top: 30px;
    min-width: 210px;
    height: 60vh;
    border: #B8E1FF 3px solid;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
