.container{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.container a {
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
}
.container a.active {
    padding: 2px 6px;
    background-color: #f1faee9c;
    color: black;
    border-radius: 30px;
}